.orderTable {
  margin: auto;
  margin-bottom: 1rem;
  width: 45%;
  border: solid 1px black;
}

.orderCaption {
  text-align: right;
  padding: 0;
}

.orderTh {
  background-color: #ffc800;
}

.orderTh,
.orderTd {
  border: solid 0.5px black;
}

.passiveInfo {
  text-align: center;
  border: none;
  width: auto;
}

.thumbnail {
  width: 7rem;
}

.p {
  margin: 0;
  margin-bottom: 0.3rem;
  font-size: 1rem;
}

/* input[type=text],
input[type=email],
input[type=tel], */
.input,
.select {
  margin-bottom: 0.3rem;
  font-size: 1.2rem;
}

.input {
  width: 13rem;
  text-align: center;
}

.days {
  text-align: center;
  width: 4rem;
}

.none {
  display: none;
}

.dateTime {
  width: 10rem;
  text-align: center;
}

#price {
  width: 7rem;
  text-align: center;
}

#address {
  width: 95%;
  font-size: 1rem;
  text-align: center;
}

input[type=submit] {
  background-color: #ffc800;
  margin-top: 1rem;
  width: 10rem;
  font-weight: bold;
  font-size: 1.5rem;
  border: none;
  margin-bottom: 1rem;
  color: white;
}

input[type=submit]:hover {
  border: none;
  color: #ffc800;
  background-color: antiquewhite;
}
